import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes for prop validation
import { Link } from 'react-router-dom';

import Main from '../layouts/Main';
import data from '../data/projects';

// Square Icon Component to display icon inside a square box
const SquareIcon = ({ icon, title }) => (
  <div className="project-icon-box">
    <img src={icon} alt={title} className="project-icon" />
  </div>
);

// Define propTypes for SquareIcon
SquareIcon.propTypes = {
  icon: PropTypes.string.isRequired, // `icon` is a required string
  title: PropTypes.string.isRequired, // `title` is a required string
};

const Projects = () => (
  <Main title="Projects" description="Learn about Harsh Bhatt's projects.">
    <article className="post" id="projects">
      <header>
        <div className="title">
          <h2>
            <Link to="/projects">Projects</Link>
          </h2>
          <p>A selection of projects that I&apos;m not too ashamed of</p>
        </div>
      </header>
      <div className="projects-list">
        {data.map((project) => (
          <div className="project-item" key={project.title}>
            {/* Display the Icon */}
            <SquareIcon icon={project.icon} title={project.title} />
            {/* Project Title */}
            <h3 className="project-title">{project.title}</h3>
            {/* Project Link */}
            <a href={project.link} target="_blank" rel="noopener noreferrer" className="project-link">
              Visit Project
            </a>
          </div>
        ))}
      </div>
    </article>
  </Main>
);

export default Projects;
